o”. Lo cual es una textura similar a granos perceptibles en la fotografía que no van a incidir en la nitidez del mismo, pero si en la percepción de textura en la imagen. Esto como resultado de la utilización de un ISO alto, ya que, entre más ISO, más textura granulada tendrá la imagen final.")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t3-i25.jpg"),"alt":"detalle del boton ISO en camara fotográfica "}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/t3-i27.svg"),"alt":"representación de guía de sensibilidad del sensor"}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_3_4"}},[_c('h2',[_vm._v("3.4 Medición y exposición")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('p',{staticClass:"mb-3"},[_vm._v(" La medición y exposición fotográfica está sometida a dispositivos electrónicos en su mayoría externos y hace referencia a valores adecuados que se deben manejar en la cámara para obtener una buena toma fotográfica. Estos valores los dará un exposímetro que hará lectura de la cantidad de luz que llega a el sensor. Como se ha dicho, la fotografía es el manejo y utilización de la luz y para ello, la cámara tiene unos dispositivos que sirven para detener el exceso de luz y ausencia de la misma. Estos mecanismos son el diafragma y el obturador, anteriormente mencionados. ")]),_c('p',{staticClass:"mb-3"},[_vm._v(" Debe haber un equilibrio en valores asignados para cada mecanismo que hará que la toma fotográfica sea adecuada, pues un ingreso de mucha luz al sensor hará que la fotografía tenga una sobreexposición y se verá negra, debido que llega mucha luz y satura el sensor. O lo contrario, una su